import { useState } from 'react';
import { PowerBIEmbed } from 'powerbi-client-react';
import { models } from 'powerbi-client';
import { Button, Snackbar, Alert } from "@mui/material";
import './Dashboard.css';

const Dashboard = () => {

    const [open, setOpen] = useState(false);
    const [success, setSuccess] = useState(false);

    const handleClick = async () => {
        console.log("HANDLING CLICK")
        try {
            const res = await fetch("https://analytics-backend-nine.vercel.app/", {
                method: "POST",
                headers: { 'Content-Type': 'application/json' }
            })
            console.log("RES", res)
            if (res.ok) {
                console.log("RES", res)
                setSuccess(true);
            }
            setOpen(true);
        } catch (error) {
            console.log("ERROR", error)
        }
    }

    return (<>
        <div style={{
            display: "flex"
        }}>
            <div style={{
                width: '30%',
                display: 'flex',
                justifyContent: 'center'
            }}>
                <Button
                    variant="contained"
                    style={{
                        backgroundColor: "#d23342",
                        padding: "20px 40px",
                        margin: "50px",
                        fontSize: "18px",
                        height: "75px"
                    }}
                    fullWidth={false}
                    onClick={() => handleClick()}
                >Obtener reporte</Button>
            </div>
            <div style={{
                width: '100%',
                display: 'flex',
                justifyContent: 'center'
            }}>
                <PowerBIEmbed
                    embedConfig={{
                        type: 'report',   // Supported types: report, dashboard, tile, visual, qna, paginated report and create
                        id: 'cc884a0b-06c6-4ba3-9897-dfe9f6046786',
                        embedUrl: 'https://app.powerbi.com/reportEmbed?reportId=cc884a0b-06c6-4ba3-9897-dfe9f6046786&ctid=344979d0-d31d-4c57-8ba0-491aff4acaed',
                        accessToken: 'eyJ0eXAiOiJKV1QiLCJhbGciOiJSUzI1NiIsIng1dCI6Ii1LSTNROW5OUjdiUm9meG1lWm9YcWJIWkdldyIsImtpZCI6Ii1LSTNROW5OUjdiUm9meG1lWm9YcWJIWkdldyJ9..M9_W5AlDh8sg08kLTJgAXUKeCbsnQdg7KrohuitaRuFjOkgdgiY61au3nOxWy8waIBvFm6brIKBYiG5PjBRom5yekv6Fk6MdLw5yb-qK85CQWcqk38vtJZWc7Q91-DZSIJd3yZEjH8TMAXNt9fUUkuyliBcP-H3KaW94AhOwvtpoBuny0LPOz-fWLKKDkVbZ3vFcvNWcBBhH6SmAVJ35SW3uudvUddBkluKsML6PsNwfO5efajmVvkNPQpXmOx4hBEOMoTh0NzBS08wV0kzWvlDj0zsmly0z8u7t1UQIZR78SIkIRdyhos9hWeybaNTeT0-1lNnyUdzB4obiOFJsUQ',
                        tokenType: models.TokenType.Aad, // Use models.TokenType.Aad for SaaS embed
                        settings: {
                            panes: {
                                filters: {
                                    expanded: false,
                                    visible: false
                                }
                            },
                            background: models.BackgroundType.Transparent,
                        }
                    }}

                    eventHandlers={
                        new Map([
                            ['loaded', function () { console.log('Report loaded'); }],
                            ['rendered', function () { console.log('Report rendered'); }],
                            ['error', function (event) { console.log(event.detail); }],
                            ['visualClicked', () => console.log('visual clicked')],
                            ['pageChanged', (event) => console.log(event)],
                        ])
                    }

                    cssClassName={"reportClass"}

                    getEmbeddedComponent={(embeddedReport) => {
                        window.report = embeddedReport;
                    }}
                />
            </div>
        </div>
        <Snackbar open={open} autoHideDuration={6000} onClose={() => setOpen(!open)}>
            <Alert onClose={() => setOpen(!open)} severity={success ? "success" : "error"} sx={{ width: '100%' }}>
                {success ? "Reporte generado!" : "Error el generar reporte"}
            </Alert>
        </Snackbar>
    </>)
}

export default Dashboard;